<template>
  <div class="router">
    <div class="card">
      <div class="card-header">
        
      </div>
      <div class="main">
        <el-form ref="form" :model="form" label-width="80px">
          <el-form-item
            label=""
            prop="title"
            :rules="[{ required: true, message: '' }]"
          >
            <el-input v-model="form.title" placeholder=""></el-input>
          </el-form-item>
          <el-form-item
            label=""
            prop="describe"
            :rules="[{ required: true, message: '' }]"
          >
            <el-input type="textarea" placeholder=""  v-model="form.describe"  :autosize="{ minRows: 8, maxRows: 8}"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit('form')"
              ></el-button
            >
            <el-button></el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import { request } from '../../untils/js/request'

export default {
  components: {
    // quillEditor
  },
  name: 'add-rule',
  data() {
    return {
      form: {
        title: '',
        dscribe: '',
      },
    }
  },
  mounted() {},
  methods: {
    async _create() {
      let data = await request('rule', this.form, 'POST')
      if (data.error_code === 10000) {
        this.$message.success('')
      }
    },
    onSubmit(form) {
      this.$refs[form].validate((valid) => {
        if (valid) {
          this._create()
        } else {
          return false
        }
      })
    },
  },
}
</script>

<style scoped>
.main {
  margin-top: 20px;
  padding: 0px 30px;
}

.add {
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: center;
}

.add-button {
  height: 36px;
  margin: auto;
}

.input {
  background-color: #fff;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #dcdfe6;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #606266;
  display: inline-block;
  font-size: inherit;
  height: 40px;
  line-height: 40px;
  outline: none;
  padding: 0 15px;
  -webkit-transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  width: 100%;
}
</style>
