<template>
  <div class="main">
    <div class="router">
      <div class="card">
        <div class="card-header">
          <el-breadcrumb>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <!---->
        <div class="search">
          <el-input
            placeholder=""
            v-model="code"
            class="input-with-select"
          >
            <el-button slot="append" icon="el-icon-search" @click="search"
              ></el-button
            >
          </el-input>
        </div>

        <el-dialog
          title=""
          :visible.sync="exchange"
          :append-to-body="true"
        >
          <el-form :model="form">
            <el-form-item
              prop="number"
              label=""
              :rules="[{ required: true, message: '' }]"
            >
              <el-input
                placeholder=""
                v-model="form.number"
                autocomplete="off"
              ></el-input>
            </el-form-item>
            <el-form-item
              label=""
              prop="egg"
              :rules="[{ required: true, message: '' }]"
            >
              <el-input
                v-model="form.egg"
                placeholder=""
                autocomplete="off"
              ></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit('form')"
                ></el-button
              >
              <el-button @click="exchange = false"></el-button>
            </el-form-item>
          </el-form>
        </el-dialog>

        <!---->
        <div class="create">
          <div style="display:flex">
            <el-button type="primary" icon="el-icon-edit" @click="create"
              ></el-button
            >
          </div>
          <span>: {{ total }} </span>
        </div>

        <!---->
        <div class="card-body text">
          <el-table :data="data" style="width: 100%">
            <el-table-column prop="id" label="ID" width="90"> </el-table-column>
            <el-table-column prop="username" label="">
              <template slot-scope="scope">
                <span
                  class="success"
                  v-if="scope.row.status"
                  v-text="scope.row.username"
                ></span>
                <span v-else></span>
              </template>
            </el-table-column>
            <el-table-column prop="code" label=""></el-table-column>
            <el-table-column prop="number" label="">
            </el-table-column>
            <el-table-column prop="create_time" label="">
            </el-table-column>
            <el-table-column label="" width="120">
              <template slot-scope="scope">
                <el-button
                  style="color:#409EFF;"
                  v-if="scope.row.status === 1"
                  type="text"
                  size="small"
                >
                  
                </el-button>
                <el-button
                  style="color:#ccc;"
                  v-if="scope.row.status === 0"
                  type="text"
                  size="small"
                >
                  
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      <!---->
      <div class="page">
        <el-pagination
          @current-change="next"
          background
          :page-size="20"
          layout="prev, pager, next"
          :total="total"
        >
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { request } from '../../untils/js/request'
import store from '../../store'

export default {
  name: 'index',
  data() {
    return {
      data: [],
      exchange: false,
      form: { number: '', egg: '' },
      code: '',
      total: 0,
      page: 1,
    }
  },
  created() {
    this._request()
  },
  methods: {
    async _request() {
      let res = await request(`/code?code=${this.code}`, '')
      if (res.error_code === 10000) {
        this.data = res.data.data
        this.total = res.data.total
        this.page = res.data.current_page
      }
    },
    create() {
      this.exchange = true
    },
    async _create() {
      let data = await request('code', this.form, 'POST')
      if (data.error_code === 10000) {
        this.$message.success('')
        this.exchange = false
        this._request()
      }
    },
    onSubmit() {
      this._create()
    },
    // 
    openEdit(id) {
      store.commit('set_id', id)
      store.commit('add_tabs', { path: 'edit-admin', name: '' })
    },
    next(e) {
      this.page = e
      this._request()
    },
    search() {
      if (this.code) {
        this._request()
      } else {
        this.$message.error('')
      }
    },
  },
}
</script>

<style scoped>
.el-form {
  margin-top: 20px;
}
</style>
