import store from "../../store"

// 
const BASE_URL = '/'
//           
const DEV_URL = '/admin'
// 
const UPLOAD_URL = '/upload'

// 
const UPLOAD_PATH = 'banner'

// 
const UPLOAD_REMOTE = 'qiniu'

/**
 * 
 * @param  path 
 * @param  or  qiniuOSS... types 
 */
export function update_info(path = UPLOAD_PATH, types = UPLOAD_REMOTE) {
    return {
        path: path,
        types: types
    }
}

/**
 * tab
 * @param {*} path 
 */
export function closeTab(path = '') {
    if (!path) {
        path = store.state.active
    }
    store.commit('close_tab', path)
}

/**
 * 
 * [[1,2], [4,8], [16,32]]
 * @param {array} array 
 */
export function cartesian(array) {
    if (!Array.isArray(array)) {
        return []
    } else {
        return array.reduce((a, b) => {
            return a.map((x) => {
                return b.map((y) => {
                    return x.concat(y)
                })
            }).reduce((a, b) => {
                return a.concat(b)
            }, [])
        }, [
            []
        ])
    }
}


/**
 * 
 * @param {} time 
 */
export function timestamp(time) {
    if (time) {
        var now = new Date(time * 1000),
            y = now.getFullYear(),
            m = now.getMonth() + 1,
            d = now.getDate()

        return y + "-" + (m < 10 ? "0" + m : m) + "-" + (d < 10 ? "0" + d : d) + " " + now.toTimeString().substr(0, 8);
    }
}



/**
 * 
 * @param {*} array 
 */
export function array_chunk(array) {
    var arr = []
    if (Array.isArray(array)) {
        array.map(item => {
            if (Array.isArray(item)) {
                let data = array_chunk(item)
                arr = data
            } else {
                arr.push(item.id)
            }
        })
    }
    return arr
}


