<template>
  <div class="main">
    <div class="router">
      <div class="card">
        <div class="card-header">
          <el-breadcrumb>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <!---->
        <div class="create">
          <el-button type="primary" v-if="false"
                     icon="el-icon-edit"
                     @click="create"></el-button>
          <span>: {{total}} </span>
        </div>
        <!---->
        <div class="card-body text">
          <el-table :data="data"
                    style="width: 100%">
            <el-table-column prop="id"
                             label="ID">
            </el-table-column>
            <el-table-column prop="username"
                             label="">
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <span style="font-size:12px;color:#f80003;cursor:pointer"
                      title=""
                      @click="change(scope.row.id)">*****</span>
              </template>
            </el-table-column>
            <el-table-column prop="create_time"
                             label="">
            </el-table-column>
            <el-table-column label="" v-if="false"
                             width="120">
              <template slot-scope="scope">
                <el-button style="color:#409EFF;"
                           type="text"
                           size="small"
                           @click="openEdit(scope.row.id)">
                  
                </el-button>
                <el-button style="color:#F56C6C;" 
                           @click="del(scope.row.id,scope.$index)"
                           type="text"
                           size="small">
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      <!---->
      <div class="page">
        <el-pagination @current-page="next"
                       background
                       layout="prev, pager, next"
                       :total="page">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import router from '../../router/index'
import { request } from "../../untils/js/request";
import store from '../../store';

export default {
  name: "index",
  data () {
    return {
      password: { 'new': '', 'old': '' },
      data: [],
      total: 0
    }
  },
  created () {
    this._request()
  },
  methods: {
    async _request () {
      let data = await request(`/admin/index`, '')
      if (data.error_code === 10000) {
        this.data = data.data.data
      }
    },
    look (src) {
      this.$alert(`<img src="${src}">`, '', {
        dangerouslyUseHTMLString: true
      });
    },
    async _del (id, key) {
      let data = await request(`admin/delete?id=${id}`, {}, 'delete')
      if (data.error_code === 10000) {
        this.data.splice(key, 1)
        this.$message({
          type: 'success',
          message: '!'
        });
      }
    },
    //  
    del (id, key) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this._del(id, key)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    // 
    openEdit (id) {
      store.commit('setid', id)
      store.commit('add_tabs', { path: 'edit-admin', name: '' })
    },
    async _password (id, text) {
      let data = await request(`/admin/update?id=${id}`, { 'password': text }, 'POST')
      if (data.error_code === 10000) {
        localStorage.removeItem('token')
        router.push({ path: '/login', name: '' })
        window.location.reload()
      }
    },
    _change (id, text) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'error',
        center: true
      }).then(() => {
        this._password(id, text)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    change (id) {
      this.$alert(``, ``, {
        dangerouslyUseHTMLString: true,
        showInput: true,
        inputPlaceholder: '',
        confirmButtonText: '',
      }).then((e) => {
        if (e.action === "confirm") {
          this._change(id, e.value)
        }
      });
      // this._password(id)
    },
    create () {
      store.commit('add_tabs', { path: 'add-admin', name: '' })
    },
    next (e) {
      this.page = e
      this._request()
    }
  }

}
</script>

<style scoped>
</style>