<template>
  <div class="main">
    <div class="router">
      <div class="card">
        <div class="card-header">
          <el-breadcrumb>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <!---->
        <div class="search">
          <el-input
            placeholder=""
            v-model="username"
            class="input-with-select"
          >
            <el-button slot="append" icon="el-icon-search" @click="search"
              ></el-button
            >
          </el-input>
        </div>
        <!---->
        <div class="create">
          <span>: {{ total }} </span>
        </div>

        <!---->
        <div class="card-body text">
          <el-table :data="data" style="width: 100%">
            <el-table-column prop="id" label="ID" width="90"> </el-table-column>
            <el-table-column prop="username" label=""> </el-table-column>
            <el-table-column prop="price" label=""></el-table-column>
            <el-table-column prop="total" label="">
              <template slot-scope="scope">
                <span
                  v-if="scope.row.status"
                  style="color:red"
                  v-text="scope.row.total"
                ></span>
                <span v-else style="color:#ccc"></span>
              </template>
            </el-table-column>
            <el-table-column prop="create_time" label="">
            </el-table-column>
            <el-table-column label="" width="120">
              <template slot-scope="scope">
                <el-button
                  style="color:#409EFF;"
                  v-if="scope.row.status === 1"
                  type="text"
                  size="small"
                >
                  
                </el-button>
                <el-button
                  style="color:#ccc;"
                  v-if="scope.row.status === 0"
                  type="text"
                  size="small"
                >
                  
                </el-button>
                <el-button
                  style="color:#fc5350;"
                  v-if="scope.row.status === 2"
                  type="text"
                  size="small"
                >
                  
                </el-button>
              </template>
            </el-table-column>
            <el-table-column label="" width="120">
              <template slot-scope="scope">
                <el-button
                  style="color:#409EFF;"
                  type="text"
                  v-if="false"
                  size="small"
                  @click="openEdit(scope.row.id)"
                >
                  
                </el-button>
                <el-button
                  style="color:#F56C6C;"
                  @click="del(scope.row.id, scope.$index)"
                  type="text"
                  size="small"
                  >
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      <!---->
      <div class="page">
        <el-pagination
          @current-change="next"
          background
          :page-size="20"
          layout="prev, pager, next"
          :total="total"
        >
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { request } from '../../untils/js/request'
import store from '../../store'

export default {
  name: 'index',
  data() {
    return {
      data: [],
      form: {},
      username: '',
      total: 0,
      page: 1,
    }
  },
  created() {
    this._request()
  },
  methods: {
    async _request() {
      let res = await request(`/order?username=${this.username}`, '')
      if (res.error_code === 10000) {
        this.data = res.data.data
        this.total = res.data.total
        this.page = res.data.current_page
      }
    },
    async _del(id, key) {
      let data = await request(`order/delete?id=${id}`, {}, 'delete')
      if (data.error_code === 10000) {
        this.data.splice(key, 1)
        this.$message({
          type: 'success',
          message: '!',
        })
      }
    },
    //  
    del(id, key) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
      })
        .then(() => {
          this._del(id, key)
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '',
          })
        })
    },
    // 
    openEdit(id) {
      store.commit('set_id', id)
      store.commit('add_tabs', { path: 'edit-admin', name: '' })
    },
    next(e) {
      this.page = e
      this._request()
    },
    search() {
      if (this.username) {
        this._request()
      } else {
        this.$message.error('')
      }
    },
  },
}
</script>

<style scoped>
.el-form {
  margin-top: 20px;
}
</style>
