<template>
  <div class="main">
    <div class="router">
      <div class="card">
        <div class="card-header">
          <el-breadcrumb>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>

          <el-button
            type="primary"
            icon="el-icon-refresh"
            circle
            @click="refresh"
          ></el-button>
        </div>
        <!---->
        <div class="search" style="display:flex">
          <el-input
            placeholder=""
            v-model="title"
            class="input-with-select"
          >
            <el-button slot="append" icon="el-icon-search" @click="search"
              ></el-button
            >
          </el-input>
        </div>
        <!---->
        <div class="create">
          <div style="display:flex">
            <el-button type="primary" icon="el-icon-edit" @click="create"
              ></el-button
            >
          </div>
          <span>: {{ total }} </span>
        </div>
        <!---->
        <div class="card-body text">
          <el-table :data="data">
            <el-table-column prop="id" width="100" label="ID"></el-table-column>
            <el-table-column prop="avatar" width="130" label="">
              <template slot-scope="scope">
                <el-avatar
                  style="width:35px;height:35px"
                  :src="scope.row.avatar"
                ></el-avatar>
              </template>
            </el-table-column>
            <el-table-column prop="username" width="150"  label=""> </el-table-column>
            <el-table-column prop="describe" label="">
            </el-table-column>
            <el-table-column prop="create_time" width="180" label="">
            </el-table-column>
            <el-table-column label="" width="200">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="primary"
                  icon="el-icon-top"
                  v-if="!scope.row.status"
                  title=""
                  @click="change(scope.row.id, scope.$index, 1)"
                >
                </el-button>
                <el-button
                  size="mini"
                  type="info"
                  title=""
                  icon="el-icon-bottom"
                  v-else
                  @click="change(scope.row.id, scope.$index, 0)"
                >
                </el-button>
                <el-button
                  size="mini"
                  type="success"
                  icon="el-icon-edit"
                  @click="_edit(scope.row.id)"
                >
                </el-button>
                <el-button
                  @click="del(scope.row.id, scope.$index)"
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                >
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      <!---->
      <div class="page">
        <el-pagination
          background
          :page-size="pagsize"
          layout="prev, pager, next"
          @current-change="handleCurrentChange"
          :total="total"
        >
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { request } from '../../untils/js/request'
import store from '../../store'
export default {
  name: 'index',
  data() {
    return {
      data: [],
      page: 1,
      pagsize: 20,
      title: '',
      total: 0,
    }
  },
  created() {
    this._request()
  },
  methods: {
    async _request() {
      let res = await request(
        `/author?page=${this.page}&username=${this.title}`
      )
      if (res.error_code === 10000) {
        this.data = res.data.data
        this.total = res.data.total
        this.page = parseInt(res.data.current_page)
      }
    },
    create() {
      store.commit('add_tabs', { path: 'add_article_author', name: '' })
      this.defaultTab = this.$route.path
    },
    async _change(id, key, status) {
      let res = await request(`/author/${id}`, { status: status }, 'PUT')
      if (res.error_code === 10000) {
        this.data[key]['status'] = status
        this.$message('')
      } else {
        this.$message.error('')
      }
    },
    async _detele(id, key) {
      let res = await request(`/author/${id}`, '', 'DELETE')
      if (res.error_code === 10000) {
        this.$message('')
        this.data.splice(key, 1)
      }
    },
    change(id, key, status) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
      }).then(() => {
        this._change(id, key, status)
      })
    },
    del(id, key) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
      }).then(() => {
        this._detele(id, key)
      })
    },
    _edit(id) {
      store.commit('set_id', id)
      store.commit('add_tabs', { path: 'edit_article_author', name: '' })
    },
    handleCurrentChange(e) {
      this.page = e
      this._request()
    },
    search() {
      if (this.title) {
        this._request()
      } else {
        this.$message.error('')
      }
    },
    refresh() {
      this.page = 1
      this._request()
    }
  },
}
</script>

<style scoped></style>
