<template>
  <div class="main">
    <div class="router">
      <div class="card">
        <div class="card-header">
          <el-breadcrumb>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>

          <el-button
            type="primary"
            icon="el-icon-refresh"
            circle
            @click="refresh"
          ></el-button>
        </div>
        <!---->
        <div class="search">
          <el-input
            placeholder=""
            v-model="title"
            class="input-with-select"
          >
            <el-button slot="append" icon="el-icon-search" @click="search"
              ></el-button
            >
          </el-input>
        </div>
        <!---->
        <div class="create">
          <div style="display:flex">
            <el-button type="primary" icon="el-icon-edit" @click="create"
              ></el-button
            >
          </div>  
        </div>
        <!---->
        <div class="card-body text">
          <el-table :data="data" style="width: 100%">
            <el-table-column prop="id" label="ID" width="80"> </el-table-column>
            <el-table-column prop="name" label=""> </el-table-column>
            <el-table-column prop="desc" label="">
            </el-table-column>
            <el-table-column prop="status" width="120" label="">
              <template slot-scope="scope">
                  <span v-if="scope.row.status" class="brand"></span>
                  <span v-else class="error"></span>
              </template>
            </el-table-column>
            <el-table-column prop="create_time" width="180" label="">
            </el-table-column>
            <el-table-column prop="status" label="" width="180">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  icon="el-icon-top"
                  v-if="!scope.row.status"
                  size="mini"
                  @click="change(scope.row.id, 1, scope.$index)"
                  title=""
                ></el-button>
                <el-button
                  type="warning"
                  icon="el-icon-bottom"
                  size="mini"
                  @click="change(scope.row.id, 0, scope.$index)"
                  title=""
                  v-else
                ></el-button>
                <el-button
                  type="info"
                  title=""
                  @click="edit(scope.row.id)"
                  icon="el-icon-edit"
                  size="mini"
                ></el-button>
                <el-button
                  type="danger"
                  title=""
                  @click="remove(scope.row.id, scope.$index)"
                  icon="el-icon-delete"
                  size="mini"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { request } from '../../untils/js/request'
import store from '../../store'

export default {
  name: 'index',
  data() {
    return {
      title: '',
      page: 1,
      pageSize: 20,
      total: 0,
      layer: false,
      data: [],
    }
  },
  created() {
    this._request()
  },
  methods: {
    async _request() {
      let res = await request(
        `role?page=${this.page}&title=${this.title}`
      )
      if (res.error_code === 10000) {
        this.data = res.data
      }
    },
    create() {
      store.commit('add_tabs', { path: 'add_auth_role', name: '' })
    },
    edit(id) {
      store.commit('add_tabs', {
        path: `edit_auth_role`,
        name: '',
        params: { id: id },
      })
    },
    handle_page(e) {
      this.page = e
      this._request()
    },
    search() {
      if (!this.title.trim().length) {
        this.$message.error('')
        return false
      }
      this._request()
    },
    refresh() {
      this.page = 1
      this.title = ''
      this._request()
    },
    async _change(id, status, key) {
      let res = await request(`role/${id}`, { status: status }, 'PUT')
      if (res.error_code === 10000) {
        this.data[key]['status'] = status
        this.$message.success(res.msg)
      } else {
        this.$message.error(res.msg)
      }
    },
    change(id, status, key) {
      let title = status ? '' : ''
      this.$confirm(`${title}, ?`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
      })
        .then(() => {
          this._change(id, status, key)
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '',
          })
        })
    },
    remove(id, key) {
      this.$confirm(`,?`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
      })
        .then(() => {
          request(`role/${id}`, '', 'DELETE').then((res) => {
            if (res.error_code === 10000) {
              this.data.splice(key, 1)
              this.$message.success('')
            } else {
              this.$message.error('')
            }
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '',
          })
        })
    },
    submit(form) {
      this.$refs[form].validate((valid) => {
        if (valid) {
          this.updatePass()
        } else {
          return false
        }
      })
    },
    reset(form) {
      this.$refs[form].resetFields()
    }
  },
}
</script>

<style scoped></style>
