<template>
  <div class="router">
    <div class="card">
      <div class="card-header">
        <span class="tag"></span>
      </div>
      <div class="main">
        <el-form ref="form" :model="form" label-width="80px">
          <el-form-item label="" prop="pid">
            <el-cascader
              v-model="value"
              :options="options"
              @change="handleChange"
            ></el-cascader>
          </el-form-item>
          <el-form-item
            label=""
            prop="menu_name"
            :rules="[{ required: true, message: '' }]"
          >
            <el-input
              placeholder=""
              v-model="form.menu_name"
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            prop="menu_desc"
            :rules="[{ required: true, message: '' }]"
          >
            <el-input
              placeholder=""
              v-model="form.menu_desc"
            ></el-input>
          </el-form-item>
          <el-form-item
            label="uri"
            prop="menu_uri"
            :rules="[{ required: form.pid, message: 'uri' }]"
          >
            <el-input
              placeholder="uri"
              v-model="form.menu_uri"
            ></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-popover
              placement="right"
              width="400"
              height="500"
              title="popover"
              visible-arrow="true"
              trigger="click"
            >
              <Icon @chooseIcon="chooseIcon"></Icon>
              <el-button class="choose" slot="reference">
                <i :class="form.icon_class"></i>
              </el-button>
            </el-popover>
          </el-form-item>
          <el-form-item label="" prop="is_show">
            <el-radio-group v-model="form.is_show">
              <el-radio-button label="1"></el-radio-button>
              <el-radio-button label="0"></el-radio-button>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" prop="status">
            <el-radio-group v-model="form.status">
              <el-radio-button label="1"></el-radio-button>
              <el-radio-button label="0"></el-radio-button>
            </el-radio-group>
          </el-form-item>
          <el-form-item
            label=""
            prop="sort"
            :rules="[{ required: true, message: '' }]"
          >
            <el-input
              placeholder=","
              v-model="form.sort"
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit('form')"
              ></el-button
            >
            <el-button type="danger" @click="reset('form')"></el-button>
            <el-button @click="close"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import { request } from '../../untils/js/request'
import { closeTab } from '../../untils/js/common'
import Icon from '../../components/icon'
export default {
  components: { Icon },
  name: 'add-auth-menu',
  data() {
    return {
      status: false,
      popover: false,
      options: [{ label: '', value: 0 }],
      value: [0],
      form: {
        pid: 0,
        menu_name: '',
        sort: '',
        menu_desc: '',
        menu_uri: '',
        avatar: '',
        icon_class: 'ri-home-3-fill',
        is_show: 1,
        status: 1,
      },
    }
  },
  mounted() {
    this.getOptions()
  },
  methods: {
    async getOptions() {
      let res = await request('menu')
      if (res.error_code === 10000) {
        res.data.map((item) => {
          this.options.push(item)
        })
      }
    },
    async _create() {
      let res = await request('/menu', this.form, 'POST')
      if (res.error_code === 10000) {
        this.$confirm(`${res.msg}, ?`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'success',
        })
          .then((res) => {
            if (res === 'confirm') {
              this.reset('form')
            }
          })
          .catch((res) => {
            if (res === 'cancel') {
              this.close()
            }
          })
      } else {
        this.$message.error(res.msg)
      }
    },
    onSubmit(form) {
      this.$refs[form].validate((valid) => {
        if (valid) {
          this._create()
        } else {
          this.$message.error('')
        }
      })
    },
    reset(form) {
      this.$refs[form].resetFields()
    },
    close() {
      closeTab()
    },
    chooseIcon(e) {
      this.form.icon_class = e
      this.popover = false
    },
    handleChange(e) {
      this.form.pid = e[e.length - 1]
    },
  },
}
</script>

<style scoped>
.main {
  margin-top: 20px;
  padding: 0px 30px;
}

.add {
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: center;
}

.add-button {
  height: 36px;
  margin: auto;
}
.choose {
  padding: 5px 13px;
}
.choose i {
  font-size: 24px;
}
.input {
  background-color: #fff;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #dcdfe6;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #606266;
  display: inline-block;
  font-size: inherit;
  height: 40px;
  line-height: 40px;
  outline: none;
  padding: 0 15px;
  -webkit-transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  width: 100%;
}
</style>
