<template>
  <div class="main">
    <div class="router">
      <div class="card">
        <div class="card-header">
          <el-breadcrumb>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>

          <el-button
            type="primary"
            icon="el-icon-refresh"
            circle
            @click="refresh"
          ></el-button>
        </div>
        <!---->
        <div class="search" style="display:flex">
          <el-select
            v-model="category"
            placeholder=""
            style="margin-right:5px;"
          >
            <el-option
              v-for="(val, key) in options"
              :key="key"
              :label="val.label"
              :value="val.value"
            >
            </el-option>
          </el-select>
          <el-select
            v-model="category"
            placeholder=""
            style="margin-right:5px;"
          >
            <el-option
              v-for="(val, key) in options"
              :key="key"
              :label="val.label"
              :value="val.value"
            >
            </el-option>
          </el-select>
          <el-input
            placeholder=""
            v-model="title"
            class="input-with-select"
          >
            <el-button slot="append" icon="el-icon-search" @click="search"
              ></el-button
            >
          </el-input>
        </div>
        <!---->
        <div class="create">
          <div style="display:flex">
            <el-button type="primary" icon="el-icon-edit" @click="create"
              ></el-button
            >
          </div>
          <span>: {{ total }} </span>
        </div>
        <!---->
        <div class="card-body text">
          <el-table :data="data">
            <el-table-column prop="id" width="100" fixed="left" label="ID"></el-table-column>
            <el-table-column prop="brand_title" width="150" label="">
            </el-table-column>
            <el-table-column prop="category_title" width="150" label="">
            </el-table-column>
            <el-table-column prop="image" width="120" label="">
              <template slot-scope="scope">
                <el-image
                  style="width: 40px; height: 40px"
                  :src="scope.row.image"
                  :preview-src-list=[scope.row.image]
                  :fit="fit"></el-image>
              </template>
            </el-table-column>
            <el-table-column prop="title" label=""  width="250"> </el-table-column>
            <el-table-column prop="price_fee" label=""  width="120"> </el-table-column>
            <el-table-column prop="market_price_fee" label=""  width="120"> </el-table-column>
            <el-table-column prop="staff" label=""  width="120"> </el-table-column>
            <el-table-column prop="create_time" width="180" label="">
            </el-table-column>
            <el-table-column label="" fixed="right" width="200">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="primary"
                  icon="el-icon-top"
                  v-if="!scope.row.status"
                  title=""
                  @click="change(scope.row.id, scope.$index, 1)"
                >
                </el-button>
                <el-button
                  size="mini"
                  type="info"
                  title=""
                  icon="el-icon-bottom"
                  v-else
                  @click="change(scope.row.id, scope.$index, 0)"
                >
                </el-button>
                <el-button
                  size="mini"
                  type="success"
                  icon="el-icon-edit"
                  @click="_edit(scope.row.id)"
                >
                </el-button>
                <el-button
                  @click="del(scope.row.id, scope.$index)"
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                >
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      <!---->
      <div class="page">
        <el-pagination
          background
          :page-size="pagsize"
          layout="prev, pager, next"
          @current-change="handleCurrentChange"
          :total="total"
        >
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { request } from '../../../untils/js/request'
import store from '../../../store'
export default {
  name: 'index',
  data() {
    return {
      data: [],
      page: 1,
      options: [{ value: 0, label: '' }],
      pagsize: 20,
      category: '',
      title: '',
      total: 0,
    }
  },
  created() {
    this._request()
    this._getCategory()
  },
  methods: {
    async _request() {
      let res = await request(
        `/product?page=${this.page}&title=${this.title}&category=${this.category}&brand=${this.category}`
      )
      if (res.error_code === 10000) {
        this.data = res.data.data
        this.total = res.data.total
        this.page = parseInt(res.data.current_page)
      } else {
        this.data = []
        this.total = 0
        this.page = 0
      }
    },
    async _getCategory() {
      this.options = [{ value: 0, label: '' }]
      let res = await request(`/product_brand?pid=1`, '')
      if (res.error_code === 10000) {
        res.data.map((item) => {
          this.options.push({
            value: item.value,
            label: item.label,
          })
        })
      }
    },
    create() {
      store.commit('add_tabs', { path: 'add-product', name: '' })
      this.defaultTab = this.$route.path
    },
    async _change(id, key, status) {
      let res = await request(`/product_brand/${id}`, { status: status }, 'PUT')
      if (res.error_code === 10000) {
        this.data[key]['status'] = status
        this.$message('')
      } else {
        this.$message.error('')
      }
    },
    async _detele(id, key) {
      let res = await request(`/product_brand/${id}`, '', 'DELETE')
      if (res.error_code === 10000) {
        this.$message('')
        this.data.splice(key, 1)
      }
    },
    change(id, key, status) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
      }).then(() => {
        this._change(id, key, status)
      })
    },
    del(id, key) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
      }).then(() => {
        this._detele(id, key)
      })
    },
    _edit(id) {
      store.commit('set_id', id)
      store.commit('add_tabs', { path: 'edit-product', name: '' })
    },
    handleCurrentChange(e) {
      this.page = e
      this._request()
    },
    search() {
      if (this.title || this.category) {
        this._request()
      } else {
        this.$message('')
      }
    },
    refresh() {
      Object.assign(this.$data, this.$options.data())
      this._request()
    },
  },
}
</script>

<style scoped></style>
