<template>
  <div class="router">
    <div class="left">
      <!--  -->
      <div class="number">
        <keep-alive v-for="(val, key) in list" :key="key">
          <number
            :title="val.title"
            :tag="val.tag"
            :number="val.number"
            :color="val.color"
          ></number>
        </keep-alive>
      </div>
      <!---->
      <keep-alive>
        <user :userData="userData"></user>
      </keep-alive>
    </div>
    <div class="right">
      <!---->
      <keep-alive>
        <cpu></cpu>
      </keep-alive>
      <!---->
      <keep-alive>
        <Index />
      </keep-alive>
    </div>
  </div>
</template>

<script>
import Index from '../server/index'
import number from '../database/number'
import Cpu from '../server/cpu'
import User from '../charts/user'
import { request } from '../../untils/js/request'

export default {
  components: {
    User,
    Cpu,
    number,
    Index,
  },
  name: 'index',
  data() {
    return {
      list: [
        { title: '', tag: '', number: 20, color: '#5fb877' },
        { title: '', tag: '', number: 24, color: '#5fb877' },
        { title: '', tag: '', number: 120, color: '#5fb877' },
        { title: '', tag: '', number: 529, color: '#5fb877' },
        { title: '', tag: '', number: 10, color: '#419df7' },
        { title: '', tag: '', number: 26, color: '#419df7' },
        { title: '', tag: '', number: 80, color: '#419df7' },
        { title: '', tag: '', number: 329, color: '#419df7' },
        { title: '', tag: '', number: 42, color: 'teal' },
        { title: '', tag: '', number: 81, color: 'teal' },
        { title: '', tag: '', number: 350, color: 'teal' },
        { title: '', tag: '', number: 1200, color: 'teal' }
      ],
      userData: {
        columns: ['', '', '', ''],
        rows: [
          { : '1/1', : 1393, : 1093, : 0.32 },
          { : '1/2', : 3530, : 3230, : 0.26 },
          { : '1/3', : 2923, : 2623, : 0.76 },
          { : '1/4', : 1723, : 1423, : 0.49 },
          { : '1/5', : 3792, : 3492, : 0.323 },
          { : '1/6', : 4593, : 4293, : 0.78 },
        ],
      },
    }
  },
  created() {},
  methods: {
    async _request() {
      let res = await request('record')
      if (res.error_code === 10000) {
        this.list = res.data
      }
    },
  },
}
</script>

<style lang="less">
.router {
  width: 100%;
  display: flex;
  justify-content: space-between;
  .left {
    width: 60%;
    .number {
      display: flex;
      flex-wrap: wrap;
      box-sizing: border-box;
      justify-content: space-between;
      .card {
        width: 21.5%;
        &:last-child {
          margin-right: 0px;
        }
      }
    }
  }
  .right {
    width: 38%;
  }
}
</style>
