<template>
  <div class="router">
    <div class="card">
      <div class="card-header">
        
      </div>
      <div class="main">
        <el-form ref="form" :model="form" label-width="80px">
          <el-form-item
            label=""
            prop="pid"
            :rules="[{ required: true, message: '' }]"
          >
            <el-cascader
              v-model="form.pid"
              placeholder=""
              :options="options"
              :props="{ checkStrictly: true }"
              clearable
            ></el-cascader>
          </el-form-item>
          <el-form-item
            label=""
            prop="title"
            :rules="[{ required: true, message: '' }]"
          >
            <el-input
              placeholder=""
              v-model="form.title"
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            prop="describe"
            :rules="[{ required: true, message: 'describe' }]"
          >
            <el-input
              type="textarea"
              placeholder=""
              v-model="form.describe"
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            prop="price"
            :rules="[{ required: true, message: '' }]"
          >
            <el-input
              placeholder=""
              v-model="form.price"
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            prop="category"
            :rules="[{ required: true, message: '' }]"
          >
            <el-select v-model="form.category" placeholder="">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>

          <el-form-item
            label=""
            prop="number"
            :rules="[{ required: true, message: '' }]"
          >
            <el-input
              placeholder=""
              v-model="form.number"
            ></el-input>
          </el-form-item>

          <el-form-item
            label=""
            prop="image"
            :rules="[{ required: true, message: '' }]"
          >
            <el-upload
              class="upload-demo"
              :action="action"
              :headers="headers"
              :file-list="image"
              :on-success="imageSuccess"
              :on-remove="imageRemove"
              list-type="picture"
            >
              <el-button size="small" type="primary"></el-button>
              <div slot="tip" class="el-upload__tip">
                jpg/png500kb
              </div>
            </el-upload>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit('form')"
              ></el-button
            >
            <el-button type="danger" @click="reset('form')"></el-button>
            <el-button></el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import { request } from '../../../untils/js/request'
import { closeTab, update_info } from '../../../untils/js/common'
import store from '../../../store'

export default {
  components: {},
  name: 'edit-category',
  data() {
    return {
      data: [],
      id: 0,
      action: this.Base + '/upload',
      headers: { token: '' },
      images: [],
      update_info: update_info,
      form: {
        pid: 0,
        title: '',
        image: '',
        desc: '',
      },
      options: [{ value: 0, label: '' }],
    }
  },
  mounted() {
    this.id = store
    this.headers.token = localStorage.getItem('token')
    this._getCategory()
  },
  methods: {
    async _getCategory() {
      let res = await request(`/product_category?pid=1`, '')
      if (res.error_code === 10000) {
        res.data.map((item) => {
          this.options.push({
            value: item.value,
            label: item.label,
          })
        })
      }
    },
    async _create() {
      let res = await request(`product_category/${this.id}`, this.form, 'PUT')
      if (res.error_code === 10000) {
        this.$message.success(res.msg)
        setTimeout(() => {
          this.close()
        }, 1500)
      }
    },
    imageSuccess(e) {
      this.form.image = e.data
    },
    imageRemove() {
      this.form.image = ''
    },
    reset(form) {
      this.$refs[form].resetFields()
    },
    close() {
      closeTab()
    },
    onSubmit(form) {
      this.$refs[form].validate((valid) => {
        if (valid) {
          this._create()
        } else {
          return false
        }
      })
    },
  },
}
</script>

<style scoped>
.main {
  margin-top: 20px;
  padding: 0px 30px;
}

.add {
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: center;
}

.add-button {
  height: 36px;
  margin: auto;
}

.input {
  background-color: #fff;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #dcdfe6;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #606266;
  display: inline-block;
  font-size: inherit;
  height: 40px;
  line-height: 40px;
  outline: none;
  padding: 0 15px;
  -webkit-transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  width: 100%;
}
</style>
