<template>
  <div class="main">
    <div class="router">
      <div class="card">
        <div class="card-header">
          <el-breadcrumb>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <!---->
        <div class="search" style="display:flex;">
          <el-input
            placeholder=""
            v-model="title"
            class="input-with-select"
          >
            <el-button slot="append" icon="el-icon-search" @click="search"
              ></el-button
            >
          </el-input>
        </div>
        <!---->
        <div class="create">
          <el-button type="primary" icon="el-icon-edit" @click="create"
            ></el-button
          >
          <span>: {{ total }} </span>
        </div>

        <!---->
        <div class="card-body text">
          <el-table :data="data" style="width: 100%">
            <el-table-column prop="id" label="ID" width="80"> </el-table-column>
            <el-table-column prop="title" label="">
            </el-table-column>
            <el-table-column prop="logo" label="">
              <template slot-scope="scope">
                <el-avatar :src="scope.row.logo"></el-avatar>
              </template>
            </el-table-column>
            <el-table-column prop="phone" label=""></el-table-column>
            <el-table-column prop="describe" label="">
            </el-table-column>
            <el-table-column prop="create_time" label="">
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="primary"
                  icon="el-icon-top"
                  v-if="!scope.row.status"
                  title=""
                  @click="change(scope.row.id, scope.$index, 1)"
                >
                </el-button>
                <el-button
                  size="mini"
                  type="info"
                  title=""
                  icon="el-icon-bottom"
                  v-else
                  @click="change(scope.row.id, scope.$index, 0)"
                >
                </el-button>
                <el-button
                  size="mini"
                  type="success"
                  icon="el-icon-edit"
                  @click="_edit(scope.row.id)"
                >
                </el-button>
                <el-button
                  @click="del(scope.row.id, scope.$index)"
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                >
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      <!---->
      <div class="page">
        <el-pagination
          @current-change="handleCurrentChange"
          background
          layout="prev, pager, next"
          :page-size="20"
          :total="total"
        >
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { request } from '../../untils/js/request'
import store from '../../store'

export default {
  name: 'index',
  data() {
    return {
      data: [],
      title: '',
      page: 1,
      total: 0
    }
  },
  created() {
    this._request()
  },
  methods: {
    async _request() {
      let res = await request(`/business?page=${this.page}&title=${this.title}`)
      if (res.error_code === 10000) {
        this.data = res.data.data
        this.total = res.data.total
        this.page = res.data.current_page
      }
    },
    create() {
      store.commit('add_tabs', { path: 'add-business', name: '' })
    },
    async _change(id, key, status) {
      let res = await request(
        `/business/${id}`,
        { status: status },
        'PUT'
      )
      if (res.error_code === 10000) {
        this.data[key]['status']=status
        this.$message.success('')
      } else {
        this.$message.error('')
      }
    },
    async _detele(id, key) {
      let res = await request(`/business/${id}`,'','DELETE')
      if (res.error_code === 10000) {
        this.$message('')
        this.data.splice(key, 1)
      }
    },
    change(id, key, status) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this._change(id, key, status)
      })
    },

    del(id, key) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this._detele(id, key)
      })
    },
    _edit(id) {
      store.commit('set_id', id)
      store.commit('add_tabs', { path: 'edit-business', name: '' })
    },
    handleCurrentChange(e) {
      this.page = e
      this._request()
    },
    search() {
      if (this.title) {
        this._request()
      } else {
        this.$message('')
      }
    }
  }
}
</script>

<style scoped>
.success {
  font-size: 14px;
  color: #fc5350;
  font-style: initial;
  margin-left: 5px;
}
</style>
