<template>
  <el-dialog
    title=""
    :append-to-body="true"
    :visible.sync="openStatus"
    width="50%"
    @close="close()"
  >
    <div class="userinfo">
      <div class="item avatar">
        <el-avatar :size="60" :src="userinfo.avatar">
          <img
            src="https://cube.elemecdn.com/e/fd/0fc7d20532fdaf769a25683617711png.png"
          />
        </el-avatar>
      </div>
      <div class="item">
        <label></label>
        <span v-text="userinfo.nickname"></span>
      </div>
      <div class="item">
        <label></label>
        <span v-if="userinfo.status" class="brand"></span>
        <span v-else class="info"></span>
      </div>
      <div class="item">
        <label></label>
        <span v-text="userinfo.phone"></span>
      </div>
      <div class="item">
        <label></label>
        <span v-text="userinfo.email"></span>
      </div>
      <div class="item" v-if="userinfo.role">
        <label></label>
        <span>
          <em
            v-for="(v, k) in userinfo.role"
            :key="k"
            class="brand"
            v-text="v.title"
          ></em>
        </span>
      </div>
      <div class="item" v-if="userinfo.create_time">
        <label></label>
        <span v-text="userinfo.create_time"></span>
      </div>
    </div>
  </el-dialog>
</template>

<script>
export default {
  props: {
    status: {
      type: Boolean,
      default: false,
    },
    userinfo: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      openStatus: this.status,
    }
  },
  watch: {
    status(val) {
      this.openStatus = val
    },
  },
  methods: {
    close() {
      this.$emit('close', false)
    },
  },
}
</script>

