<template>
  <div class="main">
    <div class="router">
      <div class="card">
        <div class="card-header">
          <el-breadcrumb>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>

          <el-button
            type="primary"
            icon="el-icon-refresh"
            circle
            @click="refresh"
          ></el-button>
        </div>

        <!---->
        <div class="create">
          <div style="display:flex">
            <el-button type="primary" icon="el-icon-edit" @click="create"
              ></el-button
            >
          </div>
        </div>
        <!---->
        <div class="card-body text">
          <el-table
            :data="data"
            row-key="id"
            default-expand-all
            :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
            style="width: 100%"
          >
            <el-table-column prop="menu_name" label="">
            </el-table-column>
            <el-table-column prop="menu_desc" label="">
            </el-table-column>
            <el-table-column width="100" prop="status" label="">
              <template slot-scope="scope">
                <span class="success" v-if="scope.row.status"></span>
                <span class="info" v-else></span>
              </template>
            </el-table-column>
            <el-table-column prop="create_time" width="200" label="">
            </el-table-column>
            <el-table-column prop="status" label="" width="180">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  title=""
                  @click="edit(scope.row.id)"
                  icon="el-icon-edit"
                  size="mini"
                ></el-button>
                <el-button
                  type="danger"
                  title=""
                  @click="remove(scope.row.id, scope.$index)"
                  icon="el-icon-delete"
                  size="mini"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { request } from '../../untils/js/request'
import store from '../../store'

export default {
  name: 'index',
  data() {
    return {
      id: 0,
      page: 1,
      pageSize: 20,
      total: 0,
      data: [],
    }
  },
  created() {
    this._request()
  },
  methods: {
    async _request() {
      let res = await request(`menu`)
      if (res.error_code === 10000) {
        this.data = res.data
      }
    },
    create() {
      store.commit('add_tabs', {
        path: 'add_menu',
        name: ''
      })
    },
    edit(id) {
      store.commit('add_tabs', {
        path: `edit_menu`,
        name: '',
        params: { id: id },
      })
    },
    handle_page(e) {
      this.page = e
      this._request()
    },
    search() {
      if (!this.username.trim().length) {
        this.$message.error('')
        return false
      }
      this._request()
    },
    refresh() {
      this.page = 1
      this.title = ''
      this._request()
    },
    async _change(id, status, key) {
      let res = await request(`menu/${id}`, { status: status }, 'PUT')
      if (res.error_code === 10000) {
        this.data[key]['status'] = status
        this.$message.success(res.msg)
      } else {
        this.$message.error(res.msg)
      }
    },
    change(id, status, key) {
      let title = status ? '' : ''
      this.$confirm(`${title}, ?`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
      })
        .then(() => {
          this._change(id, status, key)
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '',
          })
        })
    },
    remove(id, key) {
      this.$confirm(`,?`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
      })
        .then(() => {
          request(`menu/${id}`, '', 'DELETE').then((res) => {
            if (res.error_code === 10000) {
              this.data.splice(key, 1)
              this.$message.success('')
            } else {
              this.$message.error('')
            }
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '',
          })
        })
    },
  },
}
</script>

<style scoped></style>
